import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './menu.css';

const CustomNavbar = () => {
  const [openDropdown, setOpenDropdown] = useState(null);
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const toggleDropdown = (index) => {
    setOpenDropdown((prev) => (prev === index ? null : index));
  };

  const toggleMenu = () => {
    setIsMenuOpen((prev) => !prev);
  };

  return (
    <nav className="navbar-custom">
      <button className={`hamburger-menu ${isMenuOpen ? 'active' : ''}`} onClick={toggleMenu}>
         
      </button>
      <ul className={`navbar-container ${isMenuOpen ? 'active' : ''}`}>

      <li className="nav-center" style={{
    marginLeft: "1px", // 
    padding: "1px 1px", // 
    fontWeight: "bold",
    display: "flex",
    alignItems: "center"
  }}>

  <Link
    to="/"
    onClick={() => {
      window.scrollTo({ top: 0, behavior: "smooth" });
      const synth = window.speechSynthesis;
      const utterance = new SpeechSynthesisUtterance(
        " "
      );
      utterance.lang = "th-TH";
      utterance.rate = 0.7; 
      synth.speak(utterance);
    }}
    style={{ display: "flex", alignItems: "center", textDecoration: "none", color: "inherit" }}
  >
    {/*  */}
    {/* <img 
      src="/assest/img/Thairung-Logo.jpg" // 
      alt=""
      style={{ width: "45px", height: "45px", marginRight: 40 }} 
    /> */}
    
  </Link>
</li>



        <li className="dropdown" onMouseEnter={() => toggleDropdown(1)} onMouseLeave={() => toggleDropdown(1)}>
          <a></a>
          {openDropdown === 1 && (
            <ul className="dropdown-menu">
              <li><Link to="/Financial_information_1" className="text-white d-block py-1 px-3"></Link></li>
              <li><Link to="/Financial_information_2" className="text-white d-block py-1 px-3"></Link></li>
              <li><Link to="/Financial_information_3" className="text-white d-block py-1 px-3"></Link></li>
            </ul>
          )}
        </li>

        <li className="dropdown" onMouseEnter={() => toggleDropdown(2)} onMouseLeave={() => toggleDropdown(2)}>
          <a></a>
          {openDropdown === 2 && (
            <ul className="dropdown-menu">
              <li><Link to="/GLine" className="text-white d-block py-1 px-3"></Link></li>
              <li><Link to="/PriceDataP1" className="text-white d-block py-1 px-3"></Link></li>
              <li><Link to="/PriceDataP2" className="text-white d-block py-1 px-3"></Link></li>
            </ul>
          )}
        </li>

        <li className="dropdown" onMouseEnter={() => toggleDropdown(3)} onMouseLeave={() => toggleDropdown(3)}>
          <a></a>
          {openDropdown === 3 && (
            <ul className="dropdown-menu">
              <li><Link to="/Governance" className="text-white d-block py-1 px-3"></Link></li>
              <li><Link to="/Governance1" className="text-white d-block py-1 px-3"></Link></li>
              <li><Link to="/Governance3" className="text-white d-block py-1 px-3"></Link></li>
              <li><Link to="/Governance2" className="text-white d-block py-1 px-3">  </Link></li>
            </ul>
          )}
        </li>
        <li className="dropdown" onMouseEnter={() => toggleDropdown(8)} onMouseLeave={() => toggleDropdown(8)}>
          <a></a>
          {openDropdown === 8 && (
            <ul className="dropdown-menu">
              <li><Link to="/Marketmny" className="text-white d-block py-1 px-3"></Link></li>
              <li><Link to="/NewsPublish" className="text-white d-block py-1 px-3"></Link></li>
              <li><Link to="/NewsElectronic" className="text-white d-block py-1 px-3"></Link></li>
            </ul>
          )}
        </li>

        <li className="dropdown" onMouseEnter={() => toggleDropdown(4)} onMouseLeave={() => toggleDropdown(4)}>
          <a></a>
          {openDropdown === 4 && (
            <ul className="dropdown-menu">
              <li><Link to="/Onereport" className="text-white d-block py-1 px-3"> 56-1 Report</Link></li>
              <li><Link to="/Analyst" className="text-white d-block py-1 px-3"></Link></li>
            </ul>
            
          )}
        </li>
        <li className="dropdown" onMouseEnter={() => toggleDropdown(5)} onMouseLeave={() => toggleDropdown(5)}>
          <a></a>
          {openDropdown === 5 && (
            <ul className="dropdown-menu">
              <li><Link to="/shareholder" className="text-white d-block py-1 px-3"></Link></li>
              <li><Link to="/holder_Structure" className="text-white d-block py-1 px-3"></Link></li>
              <li><Link to="/holder_meeting" className="text-white d-block py-1 px-3"></Link></li>
              <li><Link to="/PolicyPayment" className="text-white d-block py-1 px-3"></Link></li>
            </ul>
          )}
        </li>

        <li className="dropdown" onMouseEnter={() => toggleDropdown(6)} onMouseLeave={() => toggleDropdown(6)}>
          <a></a>
          {openDropdown === 6 && (
            <ul className="dropdown-menu">
              <li><Link to="/Connak" className="text-white d-block py-1 px-3"></Link></li>
              {/* <li><Link to="/#" className="text-white d-block py-1 px-3"></Link></li> */}
            </ul>
          )}
        </li>
      </ul>
    </nav>
  );
};

export default CustomNavbar;
