import React, { useEffect, useState } from "react";
import axios from "axios"; //  axios 
import Menu from "../../../Components/Menu/menu";
import "./gover1.css"; //  CSS 

function Index() {
  const [manuals, setManuals] = useState([]); //  state 
  const [loading, setLoading] = useState(true); // 
  const [error, setError] = useState(null); // 

  //  useEffect  API
  useEffect(() => {
    axios
      .get(process.env.REACT_APP_API_KEY + "/api/manuals")
      .then((response) => {
        if (response.data && response.data.length > 0) {
          const latestManual = response.data.reduce((latest, current) => {
            return new Date(latest.date) > new Date(current.date) ? latest : current;
          });
          setManuals([latestManual]); // 
        } else {
          setError(" API ");
        }
        setLoading(false); // 
      })
      .catch((error) => {
        console.error("Error fetching manuals:", error);
        setError(""); // 
        setLoading(false); // 
      });
  }, []);

  // 
  if (loading) {
    return <div className="loading">...</div>;
  }

  // 
  if (error) {
    return <div className="error">{error}</div>;
  }

  return (
    <div>
      <Menu />

      {/* Hero Section */}
      <div
        className="container-fluid py-5 sticky-service"
        style={{
          backgroundImage: `url(${process.env.PUBLIC_URL}/assest/img/15.png)`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          height: "45vh",
        }}
      >
        <div className="container py-5">
          <div className="text-center mx-auto pb-5" style={{ maxWidth: 800 }}>
            <h1
              className="display-3 text-capitalize mb-3"
              style={{ color: "white", marginTop: "30px" }}
            >
              
            </h1>
          </div>
        </div>
      </div>

      {/* Hover Card Section */}
      <div
  className="container-fluid about overflow-hidden py-5 wow fadeInUp"
  data-wow-delay="0.4s"
  style={{ backgroundColor: "white" }}
>
  <div className="container py-5">
    <div
      className="row"
      style={{ display: "flex", justifyContent: "center", alignItems: "center", height: "100%" }}
    >
      {manuals.map((manual) => (
        <div className="col-lg-6 col-md-12" key={manual.id}>
          {/* Title  */}
          <div
            style={{
              textAlign: "center",
              marginBottom: "30px", //  title 
              fontSize: "24px",
              fontWeight: "bold",
              color: "#333",
            }}
          >
            {manual.title}
          </div>
          {/*  */}
          <div
  className="hover-card"
  style={{
    position: "relative",
    backgroundImage: `url(${process.env.PUBLIC_URL}/assest/img/Thairung-Logo.jpg)`,
    backgroundSize: "contain", // 
    backgroundRepeat: "no-repeat", // 
    backgroundPosition: "center", // 
    borderRadius: "10px",
    color: "white",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    textAlign: "center",
    height: "250px",
    cursor: "pointer",
    transition: "transform 0.3s ease-in-out",
  }}
  onMouseEnter={(e) => {
    e.currentTarget.style.transform = "scale(1.05)";
  }}
  onMouseLeave={(e) => {
    e.currentTarget.style.transform = "scale(1)";
  }}
  onClick={() => window.open(`${process.env.REACT_APP_PDF_KEY}/uploads/pdf_files/${manual.pdf_url}`, "_blank")}
/>

          {/*  */}
          <div
            style={{
              textAlign: "center",
              marginTop: "10px", // 
              fontSize: "14px",
              color: "gray",
            }}
          >
            : {manual.date}
          </div>
        </div>
      ))}
    </div>
  </div>
</div>

    </div>
  );
}

export default Index;
