import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css'; // Bootstrap CSS
import './P3.css';
import axios from 'axios'; // Import axios
import Menu from '../../../Components/Menu/menu';

function Index() {
  const [searchYear, setSearchYear] = useState(''); // 
  const [cardData, setCardData] = useState([]); //  API
  const [filteredData, setFilteredData] = useState([]); // 

  //  API
  const fetchCardData = async () => {
    try {
      const response = await axios.get(process.env.REACT_APP_API_KEY + "/api/detailgenerations");
  
      //  API  null  undefined
      if (!response.data || !Array.isArray(response.data)) {
        throw new Error("Invalid data received from API");
      }
  
      //  Quater  index 0
      let sortedData = response.data.sort((a, b) => {
        if (b.year !== a.year) {
          return b.year - a.year; // 
        }
        const quaterA = a.quater ? a.quater : ""; //  null  undefined
        const quaterB = b.quater ? b.quater : "";
        return quaterB.localeCompare(quaterA); //  Q1, Q2, Q3 
      });
  
      // 
      setCardData([...sortedData]);
      setFilteredData([...sortedData]);
  
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  };
  
  

  // 
  const handleSearch = (event) => {
    const year = event.target.value; //  Input
    setSearchYear(year);

    // 
    if (year.trim() === '') {
      //  
      setFilteredData(cardData);
    } else {
      const filtered = cardData.filter((card) =>
        card.year.includes(year) // 
      );
      setFilteredData(filtered.length > 0 ? filtered : cardData); // 
    }
  };

  // 
  useEffect(() => {
    fetchCardData();
  }, []);

  return (
    <div>
      <Menu />
      {/* Hero Section */}
      <div
        className="container-fluid py-5 sticky-service"
        style={{
          backgroundImage: `url(${process.env.PUBLIC_URL}/assest/img/10.png)`,
          backgroundSize: 'cover',
          backgroundPosition: 'center',
          height: '45vh',
        }}
      >
        <div className="container py-5">
          <div
            className="text-center mx-auto pb-5"
            style={{ maxWidth: 800 }}
          >
            <h1
              className="display-3 text-capitalize mb-3"
              style={{ color: 'white', marginTop: '60px' }}
            >
              
            </h1>
          </div>
        </div>
      </div>

      {/*  */}
      <div className="container my-4">
        <div className="row">
          <div className="col-md-6 mx-auto">
            <input
              type="text"
              className="form-control"
              placeholder=""
              value={searchYear}
              maxLength={4} //  4 
              onChange={(event) => {
                const value = event.target.value;
                if (/^\d*$/.test(value)) { // 
                  handleSearch(event);
                }
              }}
            />
          </div>
        </div>
      </div>

      {/*  */}
      <div className="container-fluid py-5 bg-white">
  <div className="row" style={{ rowGap: '20px' }}>
    {filteredData.length > 0 ? (
      filteredData.map((card, index) => (
        <div key={index} className="col-lg-3 col-md-6 col-sm-12 mb-4">
          <div className="card shadow-lg border-0 rounded-3 text-center">
            <div
              className="card-body d-flex flex-column align-items-center"
              style={{
                backgroundColor: '#f8f9fa',
                borderRadius: '15px',
                padding: '20px',
                minHeight: '100px',
              }}
            >
              {/*  quater  image */}
              <h5 className="card-title text-dark mb-2">{card.quater}</h5>
              <h5 className="card-title text-dark mb-2">{card.title}</h5>
              <p className="card-text text-muted mb-3">{card.description}</p>
              <div className="d-flex justify-content-center">
                {/*  */}
                <a
                  href={`${process.env.REACT_APP_PDF_KEY}/uploads/pdf_files/${card.pdf_url}`}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="btn btn-secondary mx-2"
                >
                  
                </a>
              </div>
            </div>
          </div>
        </div>
      ))
    ) : (
      <div className="col-12 text-center">
        <p></p>
      </div>
    )}
  </div>
</div>

    </div>
  );
}

export default Index;
